$color-white: #fff;
$color-grey: #666666;

$color-text: $color-white;
$color-text-grey: $color-grey;
$color-accent:#1E9AEB;
$color-background: #121212;
$gradient-accent: linear-gradient(90deg, #109FFA 2.9%, #046AB2 96.38%);


$font-family: 'Montserrat', sans-serif;
$font-weight: 400;


$z-index: 1000;
$z-index-menu: 1001;


$transition: 0.2s linear;
$transition-menu: 0.4s ease-in-out;
$transition-menu-time: 0.4s;


$line-height: (20/16);

$text-size: 1rem; 