@import 'var';
#preloader {
  background: $color-background;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10000;
  visibility: visible;
  transition: opacity 0.3s, visibility 0s 0.3s;
}
#preloaderCircle {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 50px;
  width: 50px;
  margin: -25px 0 0 -25px;
  border: 4px $color-white solid;
  border-top: 4px $color-grey solid;
  border-right: 4px $color-grey solid;
  border-bottom: 4px $color-grey solid;
  border-radius: 50%;
  -webkit-animation: spin3 1s infinite linear;
  animation: spin3 1s infinite linear;
}

@keyframes spin3 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

html {
  overflow-y: hidden;
  &.loaded {
    overflow-y: auto;

    #preloader {
      opacity: 0;
      visibility: hidden;
    }
  }
}